.modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  width: 100%;
  height: 100%;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.modal-backdrop.modal-open {
  pointer-events: all;
  opacity: 1;
  visibility: visible;
  background-color: var(--backdrop-mobail-modal);
  padding: 20px 10px 38px;
  overflow: overlay;
  width: 100%;
  height: 100%;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.modal {
  background-color: var(--background-modal);
  max-width: 380px;
  padding: 48px 20px 40px;
  margin-bottom: 50px;
  position: relative;
  margin: auto;
  border-radius: 15px;
}

@media (min-width: 768px) {
  .modal {
    max-width: 704px;
    padding: 20px;
    display: grid;
    grid-template-columns: 264px 1fr;
    grid-column-gap: 68px;
  }
}

@media (min-width: 1280px) {
  .modal {
    max-width: 806px;
    padding: 20px 20px;
    display: grid;
    grid-template-columns: 375px 1fr;
    grid-column-gap: 16px;
  }
}

.btn__closs-modal {
  position: absolute;
  top: 10px;
  right: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  cursor: pointer;
  color: #000000;
  background: var(--just-white);
  border-radius: 50%;
  border: 1px solid rgba(0, 0, 0, 0.1);
	transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
  padding: 0;
	margin: 0;
}

.btn__closs-modal:hover,
.btn__closs-modal:focus {
  
  color: var(--secondary-text-color);
  box-shadow: 0px 8px 43px var(--secondary-text-color);
  border: 1px solid var(--secondary-text-color);
  transform: rotate(180deg);
}





.modal__movi-poster {
  max-width: 360px;
  position: relative;
}

@media (min-width: 768px) {
  .modal__movi-poster {
    width: 300px;
    height: 450px;
    margin-top: 20px;
  }
}

@media (min-width: 1280px) {
  .modal__movi-poster {
    width: 360px;
    height: 550px;
    margin-top: 0px;
  }
}

.modal > .modal__movi-poster > img {
  margin-left: auto;
  display: block;
  width: 100%;
  height: 100%;
  object-fit: fill;
  border-radius: 5px;
  margin-bottom: 20px;
}

.modal_movi-info {
  margin-top: 30px;
}

.modal__title {
  margin-bottom: 20px;
  font-weight: 500;
  font-size: 20px;
  line-height: 1.15;
  text-align: center;
}

@media (min-width: 1280px) {
  .modal__title {
    font-weight: 500;
    font-size: 30px;
    line-height: 1.07;
  }
}

.modal__info-card {
  margin-bottom: 20px;
}

.modal__about {
  margin-bottom: 20px;
}

.modal__about-title {
  margin-bottom: 8px;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.67;
  text-transform: uppercase;
}

@media (min-width: 1280px) {
  .modal__about-title {
    line-height: 1.33;
  }
}

.modal__about-text {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.67;
}

.modal.dark::-webkit-scrollbar {
  width: 12px;
}

.modal.dark::-webkit-scrollbar-track {
  background: #0000008f;
  opacity: 50%;
}

.modal.dark::-webkit-scrollbar-thumb {
  background: bisque;
  border-radius: 8px;
}

.info-card__list-parametrs {
  display: grid;
  grid-template-columns: 108px 2fr;
}

.info-card__item-point {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
  color: var(--modal-darck-text);
}

.info-card__item-paramter {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
  color: var(--infofilm-paramt-text);
}

.orig-title {
  min-height: 100%;
}

.info-card__list-parametrs > li:not(:nth-last-of-type(-n + 2)) {
  margin-bottom: 8px;
}

.info-card__list-parametrs > li > span:first-child {
  display: inline-block;
  width: 30px;
  height: 14px;
  background-color: var(--acent-modal);
  color: var(--modal-light-text);
  text-align: center;
  border-radius: 7px;
}

.info-card__list-parametrs > li > span {
  display: inline-block;
  text-align: center;
  color: var(--infofilm-paramt-text);
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
}

.info-card__list-parametrs > li > span:last-child {
  display: inline-block;
  width: 36px;
  height: 16px;
  background-color: var(--modal-darck-acent);
  color: var(--modal-darck-text);
  text-align: center;
  border-radius: 2px;
}

.modal__buttons {
  display: flex;
  justify-content: space-evenly;
  padding-left: 8px;
}

.modal__add-queue[data-queue='true'],
.modal__add-watched[data-watched='true'] {
  content: 'remove';
}

.modal__add-queue[data-queue='true'],
.modal__add-watched[data-watched='true'],
.modal__add-queue[data-queue='false'],
.modal__add-watched[data-watched='false'] {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  border-radius: 5px;
  padding: 8px 6px;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
  width: 110px;
  height: 44px;
}

.modal__add-queue[data-queue='true'],
.modal__add-watched[data-watched='true'] {
  background-color: var(--acent-modal);
  color: var(--modal-light-text);
}

.modal__add-queue[data-queue='false'],
.modal__add-watched[data-watched='false'] {
  background-color: var(--modal-light-btn-bgr);
  color: var(--modal-darck-text);
  border: 1px var(--modal-darck-text) solid;
  padding: 0;
}

.modal__add-queue[data-queue='true']:hover,
.modal__add-watched[data-watched='true']:hover {
  background-color: var(--acent-modal-hover);
  box-shadow: 0px 0px 5px #642a00;
}

.modal__add-queue[data-queue='false']:hover,
.modal__add-watched[data-watched='false']:hover {
  background-color: var(--modal-darck-acent);
  box-shadow: 0px 0px 5px #642a00;
  border: 1px var(--modal-darck-acent) solid;
}

@media (min-width: 768px) {
  .modal__add-queue[data-queue='true'],
  .modal__add-watched[data-watched='true'] {
    width: 126px;
    padding: 8px 9px;
  }
}

@media (min-width: 1280px) {
  .modal__add-queue[data-queue='true'],
  .modal__add-watched[data-watched='true'],
  .modal__add-queue[data-queue='false'],
  .modal__add-watched[data-watched='false'] {
    width: 136px;
  }
}

.modal__add-watched[disabled='true'],
.modal__add-queue[disabled='true'] {
  cursor: auto;
  background-color: #b4b4b4;
  color: #838383;
  border: none;
  border-radius: 5px;
}

.modal__add-watched[disabled='true']:hover,
.modal__add-queue[disabled='true']:hover {
  background-color: #b4b4b4;
  color: #838383;
  border: none;
  border-radius: 5px;
  box-shadow: none;
}

#ytplayer {
  width: 100%;
  height: 360px;
  margin-top: 20px;
  border-radius: 4px;

  /* Media Query для планшетов */
  @media (min-width: 768px) {
    width: 664px;
    height: 370px;
  }

  /* Media Query для десктопов */
  @media (min-width: 1024px) {
    width: 767px;
    height: 425px;
    margin-top: 16px;
  }
}

.trailerBtn {
  cursor: pointer;
  width: 300px;
  height: 100px;
  background-color: transparent;
  background-image: url('../images/trailerBtn.png');
  background-size: contain;
  background-repeat: no-repeat;
  border: none;
  transform-style: preserve-3d;
  transition: transform 0.15s, box-shadow 0.15s;
}

.trailerBtn:hover,
.trailerBtn:focus {
  border-radius: 5px;
  border: none;
  box-shadow: 0px 0px 5px #642a00;
  transform: translate(0, 0.25em);
}

.trailerBtn:hover::before,
.trailerBtn:focus::before {
  transform: translate3d(0, 0.5em, -1em);
}

.trailerBtn:active {
  transform: translate3d(0, 1em, -1em);
}

.trailerBtn:active::before {
  transform: translate3d(0, 0, -1em);
}

.trailerBtnWrap {
  width: 100%;
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.trailer-placeholder {
  width: 100%;
  height: 360px;
  margin-top: 20px;
  border-radius: 4px;

  /* Media Query для планшетов */
  @media (min-width: 768px) {
    width: 632px;
    height: 425px;
  }

  /* Media Query для десктопов */
  @media (min-width: 1024px) {
    width: 782px;
    height: 425px;
    margin-top: 16px;
  }

  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  border: none;
  background-image: url('../images/404-not-found.png');
}
.modal.modal-dark {
  background-color: var(--modal-dark-them-bcg);
}


.modal.modal-dark .btn__closs-modal {
  background-color: var(--bisquit-color);
}



.modal.modal-dark > div > h2 {
  color: var(--modal-dark-them-text);
}

.modal.modal-dark > div > div > ul > .info-card__item-point {
  color: var(--modal-dark-them-text);
}

.modal.modal-dark > div > div > ul > .info-card__item-point > span:first-child {
  background-color: var(--modal-dark-them-acent);
}

.modal.modal-dark > div > div > ul > .info-card__item-point > span:last-child {
  background-color: #ffffff1f;
  color: var(--modal-dark-them-text);
}

.modal.modal-dark > div > div > ul > .info-card__item-paramter {
  color: var(--modal-dark-them-text-acent);
}

.modal.modal-dark > div > div > h3 {
  color: var(--modal-dark-them-text);
}

.modal.modal-dark > div > div > p {
  color: var(--modal-dark-them-text);
}

.modal.modal-dark > div > div > button {
  background-color: var(--modal-dark-them-text-acent);
  color: #050505;
}

.modal.modal-dark > div > div > button:hover, .modal.modal-dark > div > div > button:focus {
  background-color: #fffbfb;
  color: #050505;
}

.modal.modal-dark > div > div > button[data-watched='true'],
.modal.modal-dark > div > div > button[data-queue='true'] {
  background-color: var(--modal-dark-them-acent);
  color: #050505;
}

.modal.modal-dark > div > div > button[data-watched='true']:hover,
.modal.modal-dark > div > div > button[data-watched='true']:focus,
.modal.modal-dark > div > div > button[data-queue='true']:hover,
.modal.modal-dark > div > div > button[data-queue='true']:focus {
  background-color: #ff7300d2;
  color: #050505;
}

.modal.modal-dark > div > div > button[disabled='true'] {
  cursor: auto;
  background-color: #b4b4b4;
  color: #838383;
  border: none;
  border-radius: 5px;
}

.modal.modal-dark > div > div > button[disabled='true']:hover, .modal.modal-dark > div > div > button[disabled='true']:focus {
  background-color: #b4b4b4;
  color: #838383;
  border: none;
  border-radius: 5px;
  box-shadow: none;
}

.modal.modal-dark > div > div > .trailerBtn {
  background-color: transparent;
  transition: all 0.15s, box-shadow 0.15s;
}

.modal.modal-dark > div > div > .trailerBtn:hover, .modal.modal-dark > div > div > .trailerBtn:focus {
  background-color: #fffbfb23;
}

